.BreadBottom {
    height: 13%;
    width: 40%;
    background: linear-gradient(#F08E4A, #e27b36);
    border-radius: 0 0 30px 30px;
    box-shadow: inset -15px 0 #c15711;
    margin: 1% auto 2% ;
    background-color: red;
}

.BreadTop {
    height: 40%;
    width: 40%;
    background: linear-gradient(#bc581e, #e27b36);
    border-radius: 50% 50% 0 0;
    box-shadow: inset -15px 0 #c15711;
    margin: 2% auto 1%;
    position: relative;
}

.Seeds1 {
    width: 10%;
    height: 15%;
    position: absolute;
    background-color: white;
    left: 30%;
    top: 60%;
    border-radius: 40%;
    transform: rotate(-20deg);
    box-shadow: inset -2px -3px #c9c9c9;
}

.Seeds1:after {
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    background-color: white;
    left: -170%;
    top: -260%;
    border-radius: 40%;
    transform: rotate(60deg);
    box-shadow: inset -1px 2px #c9c9c9;
  }
  
.Seeds1:before {
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    background-color: white;
    left: 180%;
    top: -50%;
    border-radius: 40%;
    transform: rotate(60deg);
    box-shadow: inset -1px -3px #c9c9c9;
  }

  .Seeds2 {
    width: 10%;
    height: 15%;
    position: absolute;
    background-color: white;
    left: 64%;
    top: 50%;
    border-radius: 40%;
    transform: rotate(10deg);
    box-shadow: inset -3px 0 #c9c9c9;
  }
  
  .Seeds2:before {
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    background-color: white;
    left: 150%;
    top: -130%;
    border-radius: 40%;
    transform: rotate(90deg);
    box-shadow: inset 1px 3px #c9c9c9;
  }
  

.Meat {
    width: 40%;
    height: 11%;
    background: linear-gradient(#7f3608, #702e05);
    margin: 1.5% auto;
    border-radius: 15px;
}

.Cheese {
    width: 48%;
    height: 7%;
    margin: 1.5% auto;
    background: linear-gradient(#f4d004, #d6bb22);
    border-radius: 20px;
}

.Salad {
    width: 44%;
    height: 10%;
    margin: 1.5% auto;
    background: linear-gradient(#228c1d, #91ce50);
    border-radius: 20px;
}

.Bacon {
    width: 40%;
    height: 4%;
    background: linear-gradient(#bf3813, #c45e38);
    margin: 1.5% auto;
}
@media (max-width: 1000px) and (min-width:300px) {
  .BreadTop{
    width: 65%;
    height: 30%;
  }
  .BreadBottom{
   
    width: 65%;
  }
  .Cheese{
    width: 80%;
  }
  .Bacon{
    width: 63%;
  }
  .Meat{
    width: 65%;
  }
  .Salad{
    width: 75%;
  }
}
